import React from 'react';
import Square from './square';

class Board extends React.Component {

    renderSquare(i) {
        let highlight = false;
        if (this.props.winnerMoves) {
            highlight = this.props.winnerMoves.indexOf(i) !== -1
        }
        return (<Square
            value={this.props.squares[i]}
            highlight={highlight}
            onClick={() => this.props.onClick(i)}
        />);
    }

    render() {
        return (
            <div>
                <div className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
            </div>
        );
    }
}

export default Board;
